<script>
import HotTipItem from "../../components/HotTipItem";
import HotIconItem from "../../components/HotIconItem";
import Net from "@/assets/net.js";

 var myContract = new web3.eth.Contract(Net.HOGabi, Net.HOG);

export default {
  components: { HotTipItem, HotIconItem },
  data() {
    return {
      isInvited: false,
      inviteAddress: "",
      coinList: [
        { token: "HOT", price: "0.1", total: "111,222,333,11" },
        { token: "HOG", price: "0.1", total: "111,222,333,22" },
      ],
      destroy: "--",
      addHot: 500,
      totalLock: 3000,
      Income: 5000,
      address: "",
    };
  },

  mounted() {
    var _this = this;
    this.getAddress();
    this.parentAddress()

    setTimeout(function () {
     _this.getUserRefer();
     _this.getBalance();
    }, 1000)
  },
  methods: {
    // 兑换
    onExchange() {
      location.href = "https://ht.mdex.com/?utm_source=tokenpocket#/swap";
    },
    // 质押池
    onPledge() {
      this.$router.push({ name: "pledge" });
    },
    // 流动性挖矿
    onFlowDrag() {
      location.href = "https://mdex.com/?utm_source=tokenpocket#/liquidity";
    },
    getAddress() {
      //获取地址
      web3.eth.getAccounts().then((res) => (this.address = res[0]));
    },

    connectSystem() {
      //绑定推荐关系
      var account = this.inviteUrl;
      console.log(account);
      myContract.methods
        .connectSystem(account)
        .send({ from: this.address })
        .then(function (receipt) {
          _this.isInvited = false
          alert("绑定成功！");
        });
    },

    getUserRefer() {
      //获取用户的邀请者
      var _this = this;
      myContract.methods
        .getUserRefer(this.address)
        .call({ from: this.address })
        .then(function (res) {
          if(res =="0x0000000000000000000000000000000000000000"){
             _this.isInvited = true
          }
        });
    },
    parentAddress () {
      var url = window.location.href
      var arr, r = new RegExp('address=([^&]*)(&|$)')
       if (arr = url.match(r)) this.inviteAddress=arr[1]
    }, // parentAddress

     getBalance(){ //获取余额 OK
      var _this = this;
     

      myContract.methods
        .balanceOf("0x0000000000000000000000000000000000000000")
        .call({from:_this.address})
        .then(function (res) {
           _this.destroy = res / 1e18
           console.log("0地址余额："+res / 1e18)
        });
    },
  },
};
</script>

